import opg from 'ts/opg';
import {Languages} from 'ts/util/Languages';
import {string} from 'ts/util/utils';
import UserManage from './UserManage';
import GuestManage from './GuestManage';
import Panel from 'ts/ui/Panel';
import {Cache} from 'ts/util/store';


const activityId: number = ~~opg.request['activityId'];
let subjectId: number = ~~opg.request['subjectId'];

const cache = Cache.getInstance();
let row = cache.get('rowSub');
console.log(row);


opg.api({
	'addSubjectMain!post': 'activity/AddActivityContent',
	'updateSubjectMain!post': 'activity/UpActivityContent',
});

const lng = Languages.package;


const form = $('#tbMain');
let panel: Panel = opg.wrapPanel(form, {
	title: `${lng.subjectBody}`,
	btnSearchText: `${lng.saveItemContent}`,
	btnSearchClick: function () {
		let param = form.fieldsToJson({
			title: {
				name: lng.title,
				require: true,
			},
			time0: {
				name: lng.timeFrom,
				require: true,
			},
			time1: {
				name: lng.timeTo,
				require: true,
			},
		});
		if (param) {
			param.activityId = activityId;
			param.occupyTime = param.occupyTime ? (param.occupyTime.join ? param.occupyTime.join(',') : param.occupyTime) : '';
			param.time = `${param.time0}-${param.time1}`;
			delete param.time0;
			delete param.time1;

			if (subjectId) {
				param.id = subjectId;
				opg.api.updateSubjectMain(param , ()=>{
					$.extend(row , param);
					cache.set('rowSub' , row);
				});
			}
			else {
				opg.api.addSubjectMain(param, (data) => {
					subjectId = data;
					param.id = subjectId;

					$.extend(row , param);
					cache.set('rowSub' , row);

					new GuestManage(subjectId);
					new UserManage(subjectId);

				});
			}

		}
	},
});


jQuery.datetimepicker.setLocale(Languages.current);
$('#timeFrom,#timeTo').each(function (i, elem) {
	$(elem).datetimepicker({
		datepicker: false,
		//closeOnDateSelect: true,
		format: 'H:i',
		step: 30,
	});
});
$('#beginDate').datetimepicker({
		timepicker: false,
		closeOnDateSelect: true,
		format: 'Y-m-d',
});

let optArr = [];
for (let i = 7, l = 20, s = 0, step = 30; i < l;) {
	let curH = string.padLeft(i, 2);
	let curS = string.padLeft(s, 2);
	s += step;
	let h = ~~(s / 60);
	if (h > 0) {
		i += h;
		s %= 60;
	}
	let nextH = string.padLeft(i, 2);
	let nextS = string.padLeft(s, 2);
	if (i < l) {
		let opt = `${curH}:${curS}-${nextH}:${nextS}`;
		//console.log(opt);
		optArr.push(`<option value="${opt}">${opt}</option>`);
	}
}

let selOccupyTime = $('#occupyTime').html(optArr.join(''));

if (subjectId) {
	let t = row.time?row.time.split('-'):[];
	row.time0 = t[0];
	row.time1 = t[1];
	row.begindate = row.begindate?row.begindate.split(' ')[0]: '';
	form.jsonToFields(row);

	let occ = row.occupytime?row.occupytime.split(','):[];
	let opts = selOccupyTime.find('option');
	occ.map((v,i)=>{
		opts.each((j, elem)=>{
			let opt = elem as HTMLOptionElement;
			//console.log(opt , v);
			if(opt.value === v){
				$(opt).prop('selected' , true);
				return true;
			}
		})
	});


	new GuestManage(subjectId);
	new UserManage(subjectId);
}



